import Item from './item';
// The 'Leaf' class
export default class Folder extends Item {
    constructor() {
        super(...arguments);
        this.fileItems = [];
        this.subFolderItems = [];
    }
    addFile(_file) {
        this.fileItems.push(_file);
    }
    addFolder(_folder) {
        this.subFolderItems.push(_folder);
    }
}
